<template>
  <div class="search-bar">
    <div class="search-con">
      <input
        type="search"
        class="search-text"
        v-model="input"
        @focus="searchFocus"
        @blur="searchBlur"
        @input="searchInput"
      />
      <button class="search-btn" @click="clickBtn">
        <i class="icon-fangdajing"></i>
      </button>
      <div class="search-hot-words" v-if="isShow_hotWords">
        <a href="#">我们的歌</a>
        <a href="#">冰雪奇缘</a>
        <a href="#">桥边姑娘</a>
        <a href="#">星辰大海</a>
      </div>
      
      
    </div>
    
      <div class="keywordList" :class="{drop:isShow_list}">
        <ul class="resultList">
          <li v-for="(song,index) in songArr" :key="`songArr_${index}`">
            <a href="javascript:">
              {{song.name}}-
              <span>{{song.singer}}</span>
            </a>
          </li>
        </ul>
      </div>
  </div>
</template>

<script>
import { search_input,search_btn } from "../api/search";
import { ERR_OK } from "../api/config";

export default {
  created() {},
  data() {
    return {
      input: "",
      isShow_hotWords: true,
      isShow_list: false,
      songArr: [],
      searchResult:null,

    };
  },
  computed: {},
  watch: {
    input(val){
      if(val===""){
        this.songArr=[];
      }else{
        this.isShow_hotWords=false;
      }
    }
  },
  methods: {
    searchFocus() {
      this.isShow_hotWords = false;
      if(this.input!==""){
        this.isShow_list = true;
      }   
    },
    searchBlur() {
      if(this.input===""){
        this.isShow_hotWords=true;
      }
      // this.isShow_hotWords = true;
      this.isShow_list = false;
    },
    searchInput() {
      if(this.input!==""){
        this.isShow_list = true;
      } 
      this._search_input(this.input);
    },
    _search_input(val) {
      search_input(val).then(res => {
        if (res.code === ERR_OK) {
          this.songArr = res.data.song.itemlist;
        }
      });
    },
    clickBtn(){
      if(this.input===""){
        return;
      }
      this._search_btn(this.input);
    },
     _search_btn(query) {
      search_btn(query).then(res => {
        if (res.code === ERR_OK) {
          console.log(res)
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.search-bar {
  position: relative;
  margin-bottom: 5px;
  box-sizing: border-box;
}
.search-bar .search-con {
  position: relative;
  width: 100%;
  height: 50px;
}
.search-bar .search-text {
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% - 50px);
  height: 100%;
  padding: 10px 10px;
  border: 1px solid #c9c9c9;
  border-right: none;
  font-size: 14px;
  outline: none;
  z-index: 1;
  box-sizing: border-box;
}
.search-bar .search-btn {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 50px;
  text-align: center;
  background: #fff;
  color: #616161;
  outline: none;
  border: 1px solid #c9c9c9;
  border-left: none;
  font-size: 24px;
  font-weight: bold;
  z-index: 2;
  box-sizing: border-box;
}
.search-bar .search-btn:hover {
  color: #31c27c;
  font-weight: bold;
  font-size: 25px;
}
.search-bar .search-hot-words {
  position: absolute;
  top: 15px;
  right: 80px;
  text-align: right;
  z-index: 2;
  transition: all 1s;
}
.search-bar .search-hot-words a {
  color: #757575;
  background: #eee;
  font-size: 12px;
  padding: 0 5px;
  margin: 0 5px;
}
.search-bar .search-hot-words a:hover {
  background: #31c27c;
  color: #fff;
}
.search-bar .keywordList {
  position: absolute;
  top: 50px;
  left: 0;
  width: 100%;
  border: 1px solid #c9c9c9;
  border-top: none;
  box-sizing: border-box;
  background: #fff;
  border-top: 0;
  z-index: 9;
  max-height: 0;
    visibility: hidden;
    transition: max-height .6s ease-out,visibility 1ms .6s;
    overflow: hidden;
}
.search-bar .keywordList.drop{
   max-height: 800px;
    visibility: visible;
    transition: max-height .6s ease-out;
}
.search-bar .keywordList li a {
  display: block;
  padding: 10px 15px;
  font-size: 14px;
  color: #424242;
}

.search-bar .keywordList li a span {
  color: #31c27c;
}
.search-bar .keywordList li a:hover {
  background: #31c27c;
  color: #fff;
}
.search-bar .keywordList li a:hover span {
  color: #fff;
}

</style>