<template>
  <div class="btn">
    <div class="prev-btn">
      <i class="icon-shangyishou" @click="changeMusic(-1)"></i>
    </div>
    <div class="play-btn" @click="clickBtn">
      <i :class="pauseClass"></i>
    </div>
    <div class="next-btn">
      <i class="icon-kuaijin" @click="changeMusic(1)"></i>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  props:[""],
  mounted(){
   
  },
  data() {
    return {
     
    };
  },
  computed: {
    ...mapState(["isPause",]),
    
    pauseClass: function() {
      return {
        "icon-zanting": !this.isPause,
        "icon-play_icon": this.isPause
      };
    }
  },
  watch: {

  },
  methods: {
    // changeMusic点击上一首下一首按钮
    changeMusic(n){
      this.$emit("changeMusic",n);
      

    },
    // play事件点击播放、暂停按钮
    clickBtn() {
      this.$emit("clickBtn");
    },
  }
};
</script>

<style lang="scss" scoped>
.btn {
  display: inline-block;
  height: 30px;
  vertical-align: middle;
}

.btn > div {
  display: inline-block;
  width: 30px;
  height: 30px;
  color: #ccc;
  line-height: 30px;
  text-align: center;
  font-size: 30px;
  cursor: pointer;
  margin-right: 5px;
}
.btn > div:hover {
  color: #fff;
}
</style>