<template>
  <div class="music-table">
    <el-table
      class="table"
      :data="songList"
      height="450"
      empty-text="歌曲列表为空"
      style="width: 100%"
      :row-class-name="tableRowClassName"
      @row-dblclick="dbclick"
      @cell-mouse-enter="cellEnter"
      @cell-mouse-leave="cellLeave"
    >
      <el-table-column width="40">
        <template slot-scope="scope">
          <svg-volume v-if="!isPause&&index==scope.$index"></svg-volume>
          <span v-else>{{scope.$index+1}}</span>
        </template>
      </el-table-column>
      <el-table-column  label="歌曲">
        <template slot-scope="scope">
          <span :class="{songClass:scope.$index==index}">{{scope.row.song}}</span>
        </template>
      </el-table-column>
      <!-- 播放暂停按钮列 -->
      <el-table-column width="50">
        <template slot-scope="scope">
          <music-playbtn
            :isPlay="!isPause&&index==scope.$index"
            @clickPlay="clickPlay(scope.$index)"
            v-show="showIndex==scope.$index"
          ></music-playbtn>
        </template>
      </el-table-column>
      <el-table-column prop="singer" label="歌手" width="100"></el-table-column>
      <!-- 删除按钮列 -->
      <el-table-column align="right" width="50">
        <template slot-scope="scope">
          <music-delete v-show="showIndex==scope.$index" @removeMusic="removeMusic(scope.$index)"></music-delete>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import svgVolume from "../components/svgVolume";
import musicDelete from "../components/musicDelete";
import musicPlaybtn from "../components/musicPlaybtn";

import { playPause } from "../utils/playPause";

import { mapState } from "vuex";

export default {
  props: ["audio"],
  components: {
    svgVolume,
    musicDelete,
    musicPlaybtn
  },
  data() {
    return {
      showIndex: -1
    };
  },
  computed: {
    ...mapState(["isPause", "songList", "index"])
  },
  watch: {},
  methods: {
    // 双击每行
    dbclick(row, column){
      this.clickPlay(row.index)
    },
    tableRowClassName({ row, rowIndex }) {
      // 这个方法本是用来给每行添加class的，现在用来给每行添加index
      row.index = rowIndex;
    },
    cellEnter(row, column, cell, event) {
      this.showIndex = row.index;
    },
    cellLeave(row, column, cell, event) {
      this.showIndex = -1;
    },
    clickPlay(n) {
      //   如果点击的是当前播放的这行，就改变播放状态
      if (n === this.index) {
        this.$store.commit("isPauseChange");
      } else {
        //   否则就让所点击的这行开始播放
        this.$store.commit("indexChangeTo", n);
        playPause(this);
      }
    },
    //   removeMusic删除歌曲
    removeMusic(n) {
      this.$store.commit("songListChange", n);
      // 特别注意:运行上面这条语句后，此时songList已经删除了一首
      //   如果删除的是当前正在播放的这行,

      if (this.index == n) {
        // 1、如果是播放的
        if (!this.isPause) {
          // 1.1、如果是播放的，不是最后一首，就播放下一首,
          if (n !== this.songList.length) {
            playPause(this);
          } else if (this.songList.length >= 1) {
            // 1.2、如果是播放的，是最后一首，不是仅有的一首，index减1，播放新的最后一首
            this.$store.commit("indexChange", -1);
            playPause(this);
          } else if (this.songList.length === 0) {
            // 1.3、如果是播放的，是最后一首，是仅有的一首，就令isPause为true,index为0
            this.$store.commit("indexChangeTo", 0);
            this.$store.commit("isPauseChange");
          }
        } else {
          // 如果不是播放的
          if (n !== this.songList.length) {
            // 2.1、如果不是播放的，不是最后一首，不处理
            return;
          } else if (this.songList.length >= 1) {
            // 2.2、如果不是播放的，是最后一首，不是仅有的一首，index减1，不播放
            this.$store.commit("indexChange", -1);
          } else if (this.songList.length === 0) {
            // 2.3、如果不是播放的，是最后一首，是仅有的一首，让index为0
            this.$store.commit("indexChangeTo", 0);
          }
        }
      }
      // 如果删除的是当前正在播放的前面，index就要减1
      if (this.index > n) {
        this.$store.commit("indexChange", -1);
      }
      // 如果删除的是当前正在播放的后面，没有影响
    }
  }
};
</script>

<style lang="scss" scoped>
.music-table {
  background-color: rgba(189, 188, 188, 0.5);
  box-sizing: border-box;
}
.songClass{
  color: #666768;
  font-weight: bolder;
}
.table/deep/.el-table,
.table/deep/.el-table__expanded-cell {
  background: none;
}
.table/deep/.el-table tr {
  background: none;
  cursor: default;
  user-select: none;
}
.table/deep/.el-table tr:hover>td{
  background: rgba(136, 135, 135, 0.5);
}
.table/deep/.el-table th {
  background: none;
  padding: 0;
  line-height: 46px;
}
.table/deep/.el-table td {
  padding: 0;
  height: 46px;
  line-height: 46px;
}
// 修改滚动条样式
.table/deep/.el-table__body-wrapper::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
.table/deep/.el-table__body-wrapper::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 4px;
}
.table/deep/.el-table__body-wrapper::-webkit-scrollbar-thumb:hover{
  background: #bbb;
}
.table/deep/.el-table__body-wrapper::-webkit-scrollbar-track {
  background: 0 0;
}

</style>