<template>
  <div class="title">
    <span class="return icon-zhixiang-houtui" @click="returnClick('return')" v-show="!isShow"></span>
    <div class="title-con" ref="titleCon">
      <span class="title1" :style="{'animation-play-state':playState}">{{songList[index].song}} -- {{songList[index].singer}}</span>
      <!-- <span class="title2" :style="{left :left2+'px'}">{{songList[index].song}} -- {{songList[index].singer}}</span> -->
    </div>
    <span class="prev icon-zhixiang-qianjin" @click="returnClick('prev')" v-show="isShow"></span>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { throttle } from "../utils/throttle";

export default {
  props: ["audio","isShow"],
  mounted(){
    
    this.titleCon = this.$refs.titleCon;
    this.titleConWidth=this.titleCon.offsetWidth;
    this.left2=this.titleConWidth
  },
  data() {
    return {
      left1: 0,
      left2: 3600,
      titleCon:null,
      titleConWidth:1200,
    };
  },
  computed: {
    ...mapState(["index","songList","isPause"]),
     playState(){
          return this.isPause?"paused":"running"
      },
  },
  watch: {
    
    
  },
  methods: {
    fa(){
      console.log(this.audio)
    },
    returnClick(dir){
      this.$emit("returnClick",dir)
    },
    
    move() {
      this.left1 -= 1;
      this.left2 -= 1;
      if (this.left1 == 0) {
        this.left2 = 300;
      }
      if (this.left2 == 0) {
        this.left1 = 300;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.title{
 position: relative;
 background-color: rgba(189, 188, 188, 0.5);
}
.title-con {
  position: relative;
  height: 30px;
  overflow: hidden;
  margin: 0 40px;
  
}
.return,.prev{
  position: absolute;
  top: 0;
  padding:0 5px;
  height: 30px;
  line-height: 30px;
  font-size: 20px;
  color: #fff;
  cursor: pointer;
}
.return:hover,.prev:hover{
  color: #31c27c;
}
.return{
  left: 0;
}
.prev{
  right: 0;
}
.title-con span {
  position: absolute;
  color: #fff;
  display: inline-block;
  white-space: nowrap;
  text-align: center;
  height: 30px;
  line-height: 30px;
  animation: titleMove 8s infinite linear paused;
}

@keyframes titleMove {
  0% {
    right: 0;
    transform: translateX(100%);
  }
  100% {
    right: 100%;
    transform: translateX(0);
  }
}

</style>