<template>
    <div class="">
        <i class="btn" :class="pauseClass" @click="clickPlay"></i>
    </div>
</template>

<script>
import { mapState } from "vuex";

export default {
    props:["isPlay"],
     data(){
    return{
        
    }
  },
  computed:{
        ...mapState(["isPause",]),
    pauseClass: function() {
      return {
        "icon-zanting": !this.isPlay,
        "icon-play_icon": this.isPlay
      };
    }
  },
  watch:{

  },
  methods:{
clickPlay(){
     this.$emit("clickPlay");
}
  }
}
</script>

