// playPause函数是用于解决切换音频会报错The play() request was interrupted by a new load request，
// playPause函数使用时要把this传进去
// 因为在多个组件中用到了playPause，所以把它提出来，在需要使用它的组件引入它
// playPause函数用到了audio元素，所以在组件内要把audio元素通过自定义属性传进来
export function  playPause(_this) {
    var audio = _this.audio;
      if (audio !== null) {
        if (audio.paused) {
          // 暂停
          // audio.pause();
          _this.$store.commit("isPauseChange");
        }
        audio.currentTime = 0; // 控制audio时间
        let playPromise = audio.play();
        if (playPromise) {
          playPromise
            .then(() => {
              // 音频加载成功
              setTimeout(() => {}, audio.duration * 1000); // audio.duration 为音频的时长单位为秒
            })
            .catch(() => {
              // console.log("再次")
              // 失败后继续回调
              playPause(_this);
            });
        }
      }
      
  }