<template>
  <div class="player">
    <!-- <div class="header" @click="fa">
      
    </div>-->
    <div class="main">
      <div class="left">
        <music-title :audio="audio" :isShow="isShow" @returnClick="returnClick"></music-title>
        <transition enter-active-class="animated slideInRight" leave-active-class="animated slideOutRight">
          <div class="container" v-show="!isShow">
          <search-bar class="search"></search-bar>
          <music-table class="table" :audio="audio"></music-table>
        </div>
        </transition>
        <transition enter-active-class="animated slideInLeft" leave-active-class="animated slideOutLeft">
          <div class="container" v-show="isShow">
          <music-cd ></music-cd>
        </div>
        </transition>
        
      </div>
      <div class="right">
        <music-info class="info"></music-info>
      </div>
    </div>
    <div class="footer">
      <div class="container">
        <div class="control-con">
          <change-btn @clickBtn="clickBtn" @changeMusic="changeMusic" class="btn"></change-btn>
          <progress-bar :audio="audio" class="progress"></progress-bar>
          <volume-bar :audio="audio" class="volume"></volume-bar>
        </div>
      </div>
    </div>
    <audio
      ref="audio"
      @canplay="canplay"
      @ended="ended"
      @playing="playing"
      @error="error"
      :src="musicSrc"
    ></audio>
  </div>
</template>

<script>
// 标题组件
import musicTitle from "../components/musicTitle";
import searchBar from "../components/searchBar";
import musicTable from "../components/musicTable";
import musicInfo from "../components/musicInfo";
import changeBtn from "../components/changeBtn";
import progressBar from "../components/progressBar";
import volumeBar from "../components/volumeBar";
import musicCd from "../components/musicCd";

import { playPause } from "../utils/playPause";

import { mapState } from "vuex";
export default {
  components: {
    musicTitle,
    searchBar,
    musicTable,
    musicInfo,
    changeBtn,
    progressBar,
    volumeBar,
    musicCd
  },
  created() {},
  mounted() {
    this.audio = this.$refs.audio;
  },
  data() {
    return {
      isShow: true,
      audio: null
    };
  },
  computed: {
    musicSrc() {
      // 如果songList数组为空
      if (this.songList.length === 0) {
        return "";
      }
      var a = this.songList[this.index].song + ".mp3";
      var url = require(`../static/audio/${a}`);
      return url;
    },
    isEmpty() {
      return this.songList.length === 0;
    },
    ...mapState(["isPause", "songList", "index", "isError"])
  },
  watch: {
    isPause(val) {
      if (val) {
        this.audio.pause();
      } else {
        if (this.audio.currentTime == 0) {
          playPause(this);
        } else {
          this.audio.play();
        }
      }
    }
  },
  methods: {
    fa() {
      console.log(this.audio.currentSrc);
    },
    returnClick(dir) {
      if (dir == "return") {
        this.isShow = true;
      } else {
        this.isShow = false;
      }
    },
    playing() {},
    canplay() {
      this.$store.commit("isErrorChange", false);
    },
    ended() {
      this.changeMusic(1);
    },
    error() {
      this.$store.commit("isErrorChange", true);
    },
    // clickBtn点击了播放/暂停按钮
    clickBtn() {
      if (this.isError) {
        return;
      }
      this.$store.commit("isPauseChange");
    },
    changeMusic(n) {
      if (this.isError) {
        return;
      }
      this.$store.commit("indexChange", n);
      // 用playPause代替audio.play()
      this.audio.load();
      // this.audio.play();
      playPause(this);
    }

  }
};
</script>

<style lang="scss" scoped>
.player {
  height: 100%;
  position: relative;
  margin: 0 6%;
  padding-top: 20px;
}

.main {
  position: relative;
  height: 540px;
}
.main .left {
  position: relative;
  margin-right: 440px;
  height: 540px;
  overflow: hidden;
}
.left .container{
 position: absolute;
 width: 100%;
 left: 0;
 top: 30px;
}
.main .right {
  position: absolute;
  right: 0;
  top: 0;
}

.footer {
  position: fixed;
  bottom: 25px;
  left: 0;
  right: 0;
  height: 70px;
  width: 100%;
  z-index: 99;
}
.footer .container {
  width: 88%;
  margin: 0 6%;
  display: flex;
  align-items: center;
}
.footer .control-con {
  display: flex;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 20px;
  height: 30px;
  padding: 20px;
}
.footer .control-con .progress {
  flex-grow: 1;
}
/* 页面宽度小于1000px时的样式 */
@media screen and (max-width: 1000px) {
  .player {
    margin: 0 40px;
  }
  .footer .container {
    margin: 0 40px;
    width: calc(100% - 80px);
    min-width: 920px;
  }
}
</style>