<template>
  <div class="progress clearfix">
    <span class="current-time" @click="fa">{{dealTime(currentTime)}}</span>
    <div ref="progressBar" class="progress-bar" @click="progressClick">
      <div class="progress-bg">
        <div ref="progressActive" class="progress-active" :style="{width:activeWidth+'px'}">
          <div ref="dots" class="dots" @mousedown.prevent="dotsMousedown"></div>
        </div>
      </div>
    </div>
    <span class="end-time">{{dealTime(duration)}}</span>
  </div>
</template>

<script>
import { mapState } from "vuex";
// 引入截留函数
import { throttle } from "../utils/throttle";

export default {
  props: ["audio"],

  mounted() {
    window.addEventListener("resize",this.resize)
    this.progressBar = this.$refs.progressBar;
    this.progressActive = this.$refs.progressActive;
    this.dots = this.$refs.dots;

    this.bgWidth = this.progressBar.offsetWidth;
    this.dotsWidth = this.dots.offsetWidth;
    this.newBgWidth = this.bgWidth - this.dotsWidth / 2;
  },
  data() {
    return {
      progressBar: null,
      progressActive: null,
      dots: null,
      bgWidth: null,
      dotsWidth: null,
      newBgWidth: null,
      activeWidth: 0,
      // isDrag是否在拖动进度条
      isDrag: false,
      duration: 0,
      currentTime: 0
    };
  },
  computed: {
    ...mapState(["isPause","isError"])
  },
  watch: {
    // 监听newBgWidth的变化
    newBgWidth(val){
      // newBgWidth变化时重新计算activeWidth的长度
      this.activeWidth =(this.currentTime / this.duration) *val;
        
    },
    // vue挂载时audio还是null
    audio(audio) {
      var _this = this;
      audio.removeEventListener("timeupdate",timeupdate)
      
      audio.addEventListener("canplay",canplay);
      function canplay(){
        // duration必须在oncanplay后才能获取到
        _this.duration = this.duration;
      }

      audio.addEventListener("timeupdate",timeupdate);
      function timeupdate(){
        if (!_this.isDrag) {
          _this.currentTime = this.currentTime;
          _this.activeWidth =
            (_this.currentTime / _this.duration) * _this.newBgWidth;
        }
      }
    },
    isError(val){
      if(val){
        this.duration=0
      }
    }
  },
  methods: {
    fa() {
      console.log(this.audio)
    },
    // 使用节流函数
    resize:throttle(function(){
       this.bgWidth = this.progressBar.offsetWidth;
      this.newBgWidth = this.bgWidth - this.dotsWidth / 2;
    },200) ,
    // 数字格式处理函数如果是1位就在前面补上0
    addZero(str) {
      if (typeof str == "number") {
        str = str.toString();
      }
      return str.replace(/^(\d)$/, "0$1");
    },
    // 时间处理函数
    dealTime(seconds) {
      // seconds为总时间秒数
      var hour = Math.floor(seconds / (60 * 60));
      var min = Math.floor((seconds % (60 * 60)) / 60);
      var sec = Math.floor((seconds % (60 * 60)) % 60);
      if (hour == 0) {
        return this.addZero(min) + ":" + this.addZero(sec);
      } else {
        return (
          this.addZero(hour) + ":" + this.addZero(min) + ":" + this.addZero(sec)
        );
      }
    },
    // dotsMousedown拖拽进度球
    dotsMousedown() {
      if(this.isError){
        return
      }
      var _this = this;
      this.isDrag = true;

      document.onmousemove = function(e) {
        e.stopPropagation();
        var x = e.clientX - _this.progressBar.getBoundingClientRect().x;
        if (x <= 0) {
          x = 0;
        } else if (x >= _this.newBgWidth) {
          x = _this.newBgWidth;
        }
        _this.activeWidth = x;
        _this.currentTime = (x / _this.newBgWidth) * _this.duration;
      };

      document.onmouseup = function() {
        _this.isDrag = false;
        document.onmousemove = null;
        document.onmouseup = null;
        _this.audio.currentTime=_this.currentTime;
        // _this.currentTime = _this.audioDatas.nowTime;
        // if (!_this.isPause) {
        //   if (_this.audioDatas.timer == null) {
        //     _this.audioDatas.timer = setInterval(_this.progressMove, 200);
        //   }
        // }
        // if (_this.audioDatas.audio.ended) {
        //   console.log("结束");
        // }
      };
    },
    // 点击进度条
    progressClick(e) {
      if(this.isError){
        return
      }
      var x = e.clientX - this.progressBar.getBoundingClientRect().x;
      if (x <= 0) {
        x = 0;
      } else if (x >= this.newBgWidth) {
        x = this.newBgWidth;
      }
      this.activeWidth = x;
      this.currentTime = (x / this.newBgWidth) * this.duration;
      this.audio.currentTime = this.currentTime;
    }
  }
};
</script>

<style lang="scss" scoped>
.progress {
  display: flex;
  height: 30px;
  vertical-align: middle;
}
.progress .current-time {
  float: left;
  height: 30px;
  line-height: 30px;
  font-size: 16px;
  color: #fff;
  margin-right: 10px;
  width: 64px;
  text-align: right;
  user-select: none;
}
.progress .progress-bar {
  float: left;
  position: relative;
  flex-grow: 1;
  height: 10px;
  margin: 10px 0;
  cursor: pointer;
}
.progress .progress-bar .progress-bg {
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 4px;
  background-color: rgba(0, 0, 0, 0.3);
  transition: height 0.3s ease;
}
.progress .progress-bar .progress-bg::before {
  position: absolute;
  display: block;
  content: "";
  background: #fff;
  height: 100%;
}
.progress .progress-bar .progress-bg::before {
  width: 4.5px;
  left: -4.5px;
}

.progress .progress-bar:hover .progress-bg {
  height: 4px;
}
.progress .progress-bar .progress-active {
  position: absolute;
  display: flex;
  align-items: center;
  top: 0;
  left: 0;
  background-color: #fff;
  height: 100%;
  width: 0px;
}
.progress .progress-active .dots {
  position: absolute;
  /* top: -4px; */
  right: -5px;
  width: 10px;
  height: 10px;
  background: #5775f8;
  border-radius: 50%;
  cursor: pointer;
}
.progress .progress-active .dots:hover{
    background: #0633f8;
}
.progress .end-time {
  float: left;
  color: #fff;
  height: 30px;
  line-height: 30px;
  font-size: 16px;
  margin-left: 10px;
  width: 64px;
  text-align: left;
  user-select: none;
}
</style>