import jsonp from '../common/js/jsonp'                    //引入jsonp
import { commonParams, options } from './config'        //引入config.js


// search_input在输入框输入文字
export function search_input(key) {
    const url = 'https://c.y.qq.com/splcloud/fcgi-bin/smartbox_new.fcg'

    //es6的assign将commonParams和之后的对象合并为一起，放在前面的{}之中。
    // 参数来自qq音乐网站控制台
    const data = Object.assign({}, commonParams, {
        is_xml: 0,
        key: key,
        g_tk_new_20200303: 455822543,
        loginUin: 475493503,
        hostUin: 0,
        platform: 'yqq.json',
        needNewCode: 0,
    })

    return jsonp(url, data, options)
}
// 如下为公共参数
// g_tk: 455822543,
// format: 'json',
// inCharset: 'utf8',
// outCharset: 'utf-8',
// notice: 0



// search_btn点击搜索按钮,跨域报错
export function search_btn(query) {
    const url = 'https://c.y.qq.com/soso/fcgi-bin/client_search_cp'

    const data = Object.assign({}, commonParams, {
        ct: 24,
        qqmusic_ver: 1298,
        new_json: 1,
        remoteplace: 'txt.yqq.center',
        searchid: 48280298658697641,
        t: 0,
        aggr: 1,
        cr: 1,
        catZhida: 1,
        lossless: 0,
        flag_qc: 0,
        p: 1,
        n: 10,

        w: query,
        g_tk_new_20200303: 455822543,
        loginUin: 475493503,
        hostUin: 0,
        platform: 'yqq.json',
        needNewCode: 0,
    })

    return jsonp(url, data, options)
}


